<template>
  <div>
    <div class="container">
      <el-form :model="form" :inline="true">
        <el-form-item label="">
          <el-input v-model="form.authCode" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="ID">
          <el-input v-model="form.deviceId" placeholder="ID"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch"></el-button>
          <el-button type="primary" @click="openDialog('add')"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <el-card>
      <template #header>
        <div class="card-header">
          <span class="title"></span>
        </div>
      </template>

      <el-table :data="tableData" border class="table" header-cell-class-name="table-header">
        <el-table-column prop="authCode" label="" width="280" align="center" />
        <el-table-column prop="deviceId" label="ID" width="180" align="center" />
        <el-table-column prop="platform" label="" width="120" align="center" />
        <el-table-column prop="status" label="" width="120" align="center">
          <template #default="{ row }">
            <el-tag :type="row.status === 1 ? 'success' : 'danger'">
              {{ row.status === 1 ? '' : '' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="expiresAt" label="" :formatter="formatDateTime" align="center" />
        <el-table-column prop="createdAt" label="" :formatter="formatDateTime" align="center" />
        <el-table-column label="" width="280" align="center">
          <template #default="{ row }">
            <el-button text :icon="Edit" @click="openDialog('edit', row)"></el-button>
            <el-button text :type="row.status === 1 ? 'danger' : 'success'" @click="handleStatusChange(row)">
              {{ row.status === 1 ? '' : '' }}
            </el-button>
            <el-button text :icon="CopyDocument" @click="copyInstallCommand(row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="pagination">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :current-page="form.page"
          :page-size="form.pageSize"
          :total="total"
          @current-change="handlePageChange"
        />
      </div>
    </el-card>

    <el-dialog :title="dialogTitle" v-model="dialogVisible" width="500px" :close-on-click-modal="false">
      <el-form :model="authForm" ref="authFormRef" :rules="rules" label-width="100px">
        <el-form-item label="" prop="authCode" v-if="dialogType === 'add'">
          <el-input v-model="authForm.authCode" placeholder="" disabled />
        </el-form-item>
        <el-form-item label="" prop="platform">
          <el-select v-model="authForm.platform" placeholder="">
            <el-option label="Windows" value="windows" />
            <el-option label="MacOS" value="mac" />
            <el-option label="Linux" value="linux" />
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="validDays">
          <el-date-picker
            v-model="authForm.validDays"
            type="date"
            placeholder=""
            :disabled-date="disabledDate"
          />
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-switch
            v-model="authForm.status"
            :active-value="1"
            :inactive-value="0"
            active-text=""
            inactive-text=""
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="handleSave"> </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
// @ts-nocheck
import { ref, reactive, onMounted, computed } from 'vue';
import { ElMessage, ElMessageBox } from 'element-plus';
import { Edit, CopyDocument } from '@element-plus/icons-vue';
import { formatDateTime } from '@utils/utils';
import { fetchAuthList, createAuthCode, updateAuthCode } from '@api/auth';

// 
const form = reactive({
  page: 1,
  pageSize: 10,
  authCode: '',
  deviceId: '',
});

const tableData = ref([]);
const total = ref(0);
const dialogVisible = ref(false);
const dialogType = ref('add');
const dialogTitle = computed(() => (dialogType.value === 'add' ? '' : ''));

const authForm = ref({
  authCode: '',
  validDays: '',
  status: 1,
  platform: '',
});

const rules = {
  validDays: [{ required: true, message: '', trigger: 'change' }],
  status: [{ required: true, message: '', trigger: 'change' }],
  platform: [{ required: true, message: '', trigger: 'change' }],
};

// 
const getList = async () => {
  try {
    const res = await fetchAuthList(form);
    tableData.value = res.data.list;
    total.value = res.data.pagination.total;
  } catch (error) {
    console.error(':', error);
  }
};

// 
const openDialog = (type: 'add' | 'edit', row?: any) => {
  dialogType.value = type;
  dialogVisible.value = true;
  if (type === 'edit' && row) {
    authForm.value = {
      authCode: row.authCode,
      validDays: row.expiresAt,
      status: row.status,
      platform: row.platform,
    };
  } else {
    authForm.value = {
      authCode: '',
      validDays: '',
      status: 1,
      platform: '',
    };
  }
};

// 
const disabledDate = (time: Date) => {
  return time.getTime() < Date.now() - 8.64e7;
};

// 
const handleSave = async () => {
  try {
    if (dialogType.value === 'add') {
      await createAuthCode(authForm.value);
    } else {
      await updateAuthCode(authForm.value);
    }
    ElMessage.success('');
    dialogVisible.value = false;
    getList();
  } catch (error) {
    console.error(':', error);
    ElMessage.error('');
  }
};

// 
const handleStatusChange = async (row: any) => {
  try {
    await ElMessageBox.confirm(`${row.status === 1 ? '' : ''}`, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning',
    });

    await updateAuthCode({
      authCode: row.authCode,
      status: row.status === 1 ? 0 : 1,
    });

    ElMessage.success('');
    getList();
  } catch (error) {
    console.error(':', error);
  }
};

// 
const handleSearch = () => {
  form.page = 1;
  getList();
};

// 
const handlePageChange = (page: number) => {
  form.page = page;
  getList();
};

// 
const copyInstallCommand = (row: any) => {
  const commands = {
    windows: `$auth="${row.authCode}"; irm https://zjkdongao.cn/cursor/run/win.ps1 | iex`,
    mac: `curl -fsSL https://zjkdongao.cn/cursor/run/mac.sh | sudo bash -s ${row.authCode}`,
    linux: `curl -fsSL https://zjkdongao.cn/cursor/run/linux.sh | sudo bash -s ${row.authCode}`,
  };

  const command = commands[row.platform];
  if (!command) {
    ElMessage.error('');
    return;
  }

  // 
  const copyText = (text: string) => {
    // 
    const textArea = document.createElement('textarea');
    textArea.value = text;
    document.body.appendChild(textArea);

    // 
    textArea.select();

    try {
      // 
      document.execCommand('copy');
      ElMessage.success('');
    } catch (err) {
      ElMessage.error('');
      console.error(':', err);
    }

    // 
    document.body.removeChild(textArea);
  };

  //  Clipboard API
  if (navigator.clipboard && window.isSecureContext) {
    navigator.clipboard
      .writeText(command)
      .then(() => ElMessage.success(''))
      .catch((err) => {
        console.error('Clipboard API ', err);
        copyText(command);
      });
  } else {
    copyText(command);
  }
};

onMounted(() => {
  getList();
});
</script>

<style scoped lang="scss">
.container {
  padding: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table {
  margin-top: 20px;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}

.dialog-footer {
  text-align: right;
}
</style>
